body {
  background-color: #f7f7f7;
}

#app {
  max-width: 800px;
  padding-left: 0;
  padding-right: 0;
}

.attachments {
  margin: 4px;
}

.message {
  margin-top: 0;
  margin-left: 8px;
  margin-right: 8px;
}

.system-message {
  margin-left: 0;
  margin-right: 0;
  margin-bottom: 5px;
  position: relative;
  left: 0;
  right: 0;
}

.message .media-body {
  position: relative;
  padding-bottom: 15px;
}

.system-message .media-body {
  background-color: #f0f0f0;
  padding: 1em;
  text-align: center;
  color: #777;
}

.system-message .message-text {
  font-size: 10pt;
}

.system-message .nickname {
  display: none;
}

.message .timestamp {
  display: none;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 2px;
}

.system-message .timestamp {
  display: none;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 2px;
}

.message:hover .timestamp {
  display: block;
}

.avatar.media-left {
  padding-top: 3px;
}

.avatar {
  width: 32px;
  height: 32px;
  border-radius: 50%;
}

.nickname {
  font-size: 10pt;
}

.message-body {
  font-size: 11pt;
}

.timestamp {
  font-size: 8pt;
  margin-bottom: 0;
}

.emoji {
  display: inline-block;
  background-size: 20px auto !important;
  width: 20px;
  height: 20px;
}

.emoji.pack-1 {
  background: url(/emoji/1-inline.xhdpi.40x40.png) no-repeat left top
}

.emoji.pack-2 {
  background: url(/emoji/2-inline.xhdpi.40x40.png) no-repeat left top
}

.emoji.pack-3 {
  background: url(/emoji/3-inline.xhdpi.40x40.png) no-repeat left top
}

.emoji.pack-4 {
  background: url(/emoji/4-inline.xhdpi.40x40.png) no-repeat left top
}

.emoji.pack-5 {
  background: url(/emoji/5-inline.xhdpi.40x40.png) no-repeat left top
}

.emoji.pack-6 {
  background: url(/emoji/6-inline.xhdpi.40x40.png) no-repeat left top
}

.emoji.pack-7 {
  background: url(/emoji/7-inline.xhdpi.40x40.png) no-repeat left top
}




